.page-header {
    width: 100vw;
    position: fixed;
    background-color: rgb(0,0,0,0.3);
    z-index: 2;
    left: 0;
    top: 0;
    box-sizing: border-box;
    overflow: hidden;

    &__plank {
        display: flex;
        border: none;
        height: 66px;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box; 
    }

    &--opened {
        background-color: $blue;
    }

    &--opened &__plank{ 
        height: 69px;
        border-bottom: solid 1px $black;
    }

    &--opened .main-nav{
        display: block;
    }

    &--opened .burger {
        min-width: initial;
        width: 23px;
        height: 23px;
        display: block;
        margin-right: 12px;
    }

    &--opened .burger__line {
        border-radius: 0;
        transition: 0.2s;
    }

    &--opened .burger__line:nth-child(2) {
        opacity: 0;
    }

    &--opened .burger__line:nth-child(1) {
        transform: translate(0, 200%) rotate(45deg) ;
    }

    &--opened .burger__line:nth-child(3) {
        transform: translate(0, 0) rotate(-45deg) ;
    }

    @include breakpoint (min-width, $tablet-width-Breakpoint) {
        &__plank {
            height: 110px;
        }

        &--opened &__plank{ 
            height: 109px;
        }
    }
}