@charset "UTF-8";
/* Global */
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), url("./fonts/opensans.woff2") format("woff2"), url("./fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), local("Opensans-bold"), url("./fonts/opensansbold.woff2") format("woff2"), url("./fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: bold;
}
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), local("Opensans-light"), url("./fonts/opensanslight.woff2") format("woff2"), url("./fonts/opensanslight.woff") format("woff");
  font-weight: 300;
}
* {
  -webkit-tap-highlight-color: transparent;
}

/*Передаем property= параметр условия, Breakpoint= значение условия*/
/* elements */
.burger {
  position: relative;
  border: none;
  cursor: pointer;
  background: transparent;
  z-index: 5;
  min-width: 50px;
  display: flex;
  flex-flow: column;
  row-gap: 6px;
  align-items: center;
  padding: 0;
  user-select: none;
}
.burger:hover .burger__line {
  background-color: #D22856;
}
.burger:active .burger__line {
  opacity: 0.3;
}
.burger__line {
  width: 100%;
  height: 4px;
  background-color: #FFFFFF;
  box-sizing: border-box;
  border-radius: 1px;
  transition: 0.2s;
  user-select: none;
}

.backgr:before, .backgr:after {
  background: #F2F2F2;
  content: "";
  display: block;
  height: 50%;
  left: 0;
  position: absolute;
  right: 0;
  z-index: -1;
  -webkit-backface-visibility: hidden;
}
.backgr:before {
  top: 0px;
  transform: skewY(-6deg);
  transform-origin: 100% 0;
}
.backgr:after {
  bottom: 0;
}
.backgr--deg3:before {
  transform: skewY(-3deg);
}

/* blocks */
.container {
  padding: 0 20px;
}

.page__body {
  height: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow: hidden scroll;
}

.text {
  font-family: "Opensans", Arial, sans-serif;
  line-height: 1.5;
}
.text__small {
  font-weight: 400;
  font-size: 14px;
  color: #283645;
  line-height: 1.7em;
}
.text__small--spacing {
  line-height: 2.1em;
}
.text__normal {
  font-weight: 300;
  font-size: 24px;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  font-style: normal;
}
.text__normal--black {
  color: #283645;
}
.text__normal--red {
  color: #D22856;
}
.text__normal--medium {
  font-size: 18px;
}
.text__normal--small {
  font-size: 14px;
}
.text__heading {
  font-weight: 700;
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
  font-style: normal;
  text-decoration: none;
}
.text__heading--black {
  color: #000000;
}
.text__heading--blue {
  color: #283645;
}
.text__heading--small {
  font-size: 16px;
}
.text__heading--big {
  font-size: 30px;
}

.visually-hidden {
  color: transparent;
  font-size: 0;
  height: 0;
  position: absolute;
}

.main-nav {
  width: 100%;
  display: none;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  transition: 1s ease width;
}
.main-nav__item {
  height: 63px;
  border-bottom: solid 1px #000000;
  border-top: solid 1px rgba(255, 255, 255, 0.15);
}
.main-nav__link {
  box-sizing: border-box;
  display: block;
  text-decoration: none;
  color: inherit;
  width: 100%;
  padding: 18px 0;
  text-transform: uppercase;
  transition: 0.1s;
}
.main-nav__link:hover {
  color: #D22856;
}
.main-nav__link:active {
  opacity: 0.3;
}

.slider {
  position: relative;
  touch-action: pan-y;
}
.slider__item {
  width: 100%;
  transition: 0.5s ease-in;
  opacity: 0;
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  transform: translateX(-50%);
}
.slider__item--active {
  opacity: 1;
  position: static;
  transform: translateX(0);
  transition: 0.5s ease-in;
}
.slider__toggles {
  display: flex;
  justify-content: center;
  column-gap: 15px;
  align-items: center;
  height: 12px;
}
.slider__toggle {
  padding: 0;
  margin: 0;
  cursor: pointer;
  width: 6px;
  height: 6px;
  background-color: #283645;
  opacity: 0.3;
  border: 0;
  border-radius: 6px;
  transition: 0.1s;
  transform: scale(0.9);
}
.slider__toggle:hover {
  opacity: 0.6;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__toggle--current {
  box-sizing: content-box;
  background-color: #FFFFFF;
  border: 3px solid #283645;
  opacity: 1;
}
.slider__toggle--current:hover {
  opacity: 1;
}
.slider__toggle--current:active {
  transition: 0.1s;
  transform: scale(0.75);
}

.logo {
  display: block;
}
.logo__object {
  display: block;
}
.logo__img {
  display: block;
}

.apps {
  background-color: #1D2631;
  padding-bottom: 35px;
  padding-top: 28px;
  row-gap: 30px;
}
.apps__button {
  width: 100%;
  max-width: 280px;
}
.apps__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  gap: 35px;
}
.apps__link {
  display: block;
  transition: 0.1s ease-out;
}
.apps__link:hover {
  transform: scale(1.15);
}
.apps__link:active {
  transform: scale(0.9);
}
.apps__link::before {
  content: "";
  width: 47px;
  height: 56px;
  display: block;
  background-color: white;
}
.apps__link--ios::before {
  background: url("./images/apple-logo.svg") center center no-repeat;
}
.apps__link--android::before {
  background: url("./images/android-logo.svg") center center no-repeat;
}
.apps__link--windows::before {
  background: url("./images/microsoft-logo.svg") center center no-repeat;
}
.apps__description {
  margin: 0;
}
@media only screen and (min-width: 660px) {
  .apps {
    row-gap: 35px;
  }
  .apps__button {
    max-width: 100%;
    margin-bottom: 10px;
  }
  .apps__description {
    font-size: 18px;
    line-height: 1.4;
  }
}

.button {
  background: inherit;
  border: 3px solid #FFFFFF;
  border-radius: 30px;
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;
  padding: 14px 14px;
  transition: 0.1s;
}
.button:hover {
  background-color: #FFFFFF;
  color: #D22856;
}
.button:active {
  color: #F2BFCC;
}

.social-list {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: center;
  column-gap: 11px;
}
.social-list__link {
  display: block;
  border: 3px solid #EAEBEC;
  border-radius: 48px;
}
.social-list__link::before {
  display: block;
  content: "";
  width: 48px;
  height: 48px;
}
.social-list__link--twitter::before {
  background-image: url("./images/twitter.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.social-list__link--facebook::before {
  background-image: url("./images/facebook.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.social-list__link--youtube::before {
  background-image: url("./images/youtube.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.social-list__link:hover {
  border-color: #283645;
}
.social-list__link:active {
  opacity: 0.1;
}

/* Section */
.download {
  position: relative;
}
.download__wrap {
  background: url("./images/back-mobile.jpg") center center no-repeat;
  background-size: cover;
  padding-top: 66px;
}
.download__title {
  margin-bottom: 35px;
  margin-top: 30px;
}
.download__demo {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}
.download__choise {
  display: flex;
  flex-flow: column;
  align-items: center;
}
@media only screen and (min-width: 660px) {
  .download__wrap {
    padding-top: 184px;
  }
  .download__title {
    width: 300px;
    position: absolute;
    margin-top: 0;
    margin-left: 20px;
    font-weight: 300;
    font-size: 40px;
    line-height: 1.2;
  }
  .download__demo {
    margin-right: 0;
  }
  .download__choise {
    position: absolute;
    bottom: 80px;
    background: none;
    width: 300px;
  }
}

.page-header {
  width: 100vw;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
  left: 0;
  top: 0;
  box-sizing: border-box;
  overflow: hidden;
}
.page-header__plank {
  display: flex;
  border: none;
  height: 66px;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.page-header--opened {
  background-color: #283645;
}
.page-header--opened .page-header__plank {
  height: 69px;
  border-bottom: solid 1px #000000;
}
.page-header--opened .main-nav {
  display: block;
}
.page-header--opened .burger {
  min-width: initial;
  width: 23px;
  height: 23px;
  display: block;
  margin-right: 12px;
}
.page-header--opened .burger__line {
  border-radius: 0;
  transition: 0.2s;
}
.page-header--opened .burger__line:nth-child(2) {
  opacity: 0;
}
.page-header--opened .burger__line:nth-child(1) {
  transform: translate(0, 200%) rotate(45deg);
}
.page-header--opened .burger__line:nth-child(3) {
  transform: translate(0, 0) rotate(-45deg);
}
@media only screen and (min-width: 660px) {
  .page-header__plank {
    height: 110px;
  }
  .page-header--opened .page-header__plank {
    height: 109px;
  }
}

.benifits {
  padding-top: 46px;
}
.benifits__slogan {
  margin: 0 0 30px;
}
.benifits__list-wrap {
  position: relative;
  z-index: 1;
  padding-top: 85px;
}
.benifits__list {
  list-style: none;
  padding: 0;
  margin-top: 0px;
  margin-bottom: 30px;
  text-align: center;
  display: flex;
  flex-flow: column;
  gap: 20px;
  overflow: hidden;
}
.benifits__list:last-child {
  margin-bottom: 0;
}
.benifits__title {
  margin-top: 0;
  margin-bottom: 16px;
}
.benifits__item-img {
  min-height: 230px;
  position: relative;
}
.benifits__image {
  display: block;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  bottom: -45px;
  transform: translateX(-50%);
  object-fit: contain;
}
@media only screen and (min-width: 660px) {
  .benifits {
    padding-top: 56px;
    overflow: hidden;
  }
  .benifits__slogan {
    margin: 0;
    font-weight: 300;
    font-size: 40px;
    line-height: 1.35;
  }
  .benifits__slogan:nth-child(2) br {
    display: none;
  }
  .benifits__list-wrap {
    display: flex;
    flex-direction: row-reverse;
    padding-top: 28px;
  }
  .benifits__list {
    width: 50%;
    overflow: visible;
  }
  .benifits__list:nth-child(1) {
    margin-bottom: 0;
    margin-top: 54px;
    gap: 25px;
    text-align: left;
  }
  .benifits__title {
    text-align: left;
  }
  .benifits__item-img {
    min-height: 480px;
  }
  .benifits__image {
    bottom: initial;
    top: 0px;
  }
}

.research {
  display: none;
}
@media only screen and (min-width: 660px) {
  .research {
    display: flex;
    padding-top: 54px;
    justify-content: space-between;
  }
  .research__wrap {
    width: 50%;
    padding-top: 15px;
  }
  .research__title {
    text-align: left;
    line-height: 1.2;
    margin-bottom: 20px;
  }
  .research__text {
    margin-bottom: 20px;
  }
  .research__link {
    color: #D22856;
  }
  .research__link:hover {
    color: #283645;
  }
  .research__link:active {
    opacity: 0.3;
  }
}

.reviews {
  padding-top: 45px;
  padding-bottom: 35px;
}
.reviews__wrap {
  padding-bottom: 37px;
}
.reviews__list {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
}
.reviews__title {
  margin: 0 0 20px;
}
.reviews__toggles {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 0;
}

.contacts {
  padding-top: 40px;
  padding-bottom: 35px;
}
.contacts__slogan {
  margin: 0;
}
.contacts__info {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 25px;
}
.contacts__head {
  margin: 0;
}
.contacts__link {
  margin: 0;
  overflow-wrap: anywhere;
}
.contacts__link[href]:hover {
  color: #283645;
  text-decoration-line: underline;
}
.contacts__link[href]:active {
  opacity: 0.3;
  text-decoration-line: none;
}

.page-footer {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  row-gap: 40px;
}
.page-footer__social {
  padding: 0;
}
.page-footer__copyright {
  font-style: normal;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-footer__copyright::after {
  content: "";
  display: block;
  width: 27px;
  height: 34px;
  background: url("./images/htmlacademy.svg") center no-repeat;
  margin-left: 15px;
}

.tariff {
  overflow: hidden;
  position: relative;
}
.tariff__wrap {
  padding-top: 100px;
  padding-bottom: 57px;
  overflow: hidden;
}
.tariff__table {
  border-collapse: collapse;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.5s ease-in-out;
  margin-bottom: 42px;
}
.tariff__toggles {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.tariff__column-head {
  background-color: #283645;
  border-left: 3px solid #E5E5E5;
  text-align: center;
  height: 94px;
}
.tariff__column-head .tariff__text:nth-child(1) {
  margin-top: 0;
  margin-bottom: 10px;
}
.tariff__column-head .tariff__text:nth-child(2) {
  margin: 0;
}
.tariff__column-head:nth-child(2) {
  border-left: 3px solid #283645;
}
.tariff__column-head:last-child {
  border-right: 3px solid #283645;
}
.tariff__column-head--empty {
  background-color: inherit;
  display: none;
}
.tariff__column-head--special::before {
  content: "";
}
.tariff__line .tariff__value:nth-child(n+1) {
  border-top: 3px solid #283645;
}
.tariff__line-head {
  display: none;
}
.tariff__value {
  min-width: 87.5vw;
  height: 59px;
  background-color: #FFFFFF;
  border: 3px solid #E5E5E5;
}
.tariff__value--yes {
  background-image: url("./images/yes.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.tariff__value--no {
  background-image: url("./images/no.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.map__frame {
  display: block;
  clip-path: polygon(0% 0%, 50% 15%, 100% 0%, 100% 100%, 0% 100%);
}